  >
    <span
      class="avatar avatar-lg rounded-circle tk-layout-inline-block tk-border-color-white tk-bg-color-gray-200"
      v-if="showIcon"
    >
      <img :src="data.icon" />
    </span>
    <span
      class="tk-layout-inline-block"
      :class="{ 'tk-px-margin-left-px20': showIcon }"
      :style="{ width: showIcon ? 'calc(100% - 20px - 80px)' : '100%' }"
    >
      <div class="tk-font-weight-bold">
        <span
          class="avatar avatar-xs rounded-circle tk-bg-color-gray-200 tk-layout-inline-block tk-px-margin-right-px8"
