<script>
export default {
  props: {
    data: {
      default() {
        return {};
      },
    },
    showRemain: {
      type: Boolean,
      default: false,
    },
    showUser: {
      type: Boolean,
      default: true,
    },
    showTeacher: {
      type: Boolean,
      default: false,
    },
    showIcon: {
      type: Boolean,
      default: true,
    },
  },
  created() {
    // console.log(this.data);
  },
  methods: {
    showLessonDetail() {
      this.$emit("showLessonDetail", this.data);
    },
    isNull(data) {
      return this.$tools.isNull(data)
    },
    getFirstLetter(str) {
      return this.$tools.getFirstLetter(str)
    }
  },
};
</script>